<template>
  <b-container fluid="md">
    <b-row>
      <!-- <b-dropdown
        text="Pilih Nomor Order"
        variant="dark"
        class="mt-2"
        menu-class="w-100"
        @show="dd_event"
      >
        <b-dropdown-item 
          v-for="code in po_items" 
          :key="code.po_code" 
          @click="dd_item_click(code.po_code)"
        >
          {{code.po_code}}
        </b-dropdown-item>
      </b-dropdown> -->
      <b-row class="my-2 mx-0">
        <b-button block v-b-modal.qrscan-modal variant="dark"> Scan QR </b-button>
      </b-row>
      <b-modal
        ref="qrscan-modal-ref"
        centered
        hide-header
        hide-footer
        id="qrscan-modal"
      >
        Scan QR Code
        <qrcode-stream @decode="onDecode" @init="onInit"></qrcode-stream>
        <b-form class="container my-2" @submit="onsubmitdecode" >
          <b-row>
            <b-col>
              <b-form-input type="text" id="text-user" placeholder="Tulis QR Code" v-model="form_qr"></b-form-input>
            </b-col>
            <b-col>
              <b-button class="m-3 " @click="onDecode(form_qr)" size="sm" variant="primary">Kirim</b-button> 
            </b-col>
          </b-row>    
        </b-form>
        <b-button class="mt-3" variant="secondary" @click="hide_modal('qrscan-modal-ref')">
          Tutup
        </b-button>
      </b-modal>
      <b-alert class="mt-2 container" :show="qr_alert" dismissible variant="danger">
        Kode Salah!
      </b-alert>
    </b-row>
    <b-row class="mx-1 my-4">
      <b-avatar 
        class="mx-2 my-1" 
        v-for="op in operator_assign"
        :key="op.name"
        button 
        @click="op_click(op.name, op.material)"
        v-b-modal.operator-modal 
        variant="secondary"
      >     
      </b-avatar>
      <!-- <b-avatar class="mx-4" button variant="secondary"></b-avatar>
      <b-avatar class="mx-4" button variant="secondary"></b-avatar> -->
      <!-- "op_click(op.name, op.material)" -->

      <b-modal
        ref="operator-modal-ref"
        centered
        hide-header
        hide-footer
        id="operator-modal"
      >
        <div class="accordion" role="tablist">
          <b-card no-body class="mb-1">
            <b-card-header header-tag="header" class="p-1" role="tab">
            <!-- <b-row class="mx-0"><b-button v-b-toggle.accordion-1 variant="secondary">PO 1</b-button></b-row> -->
            </b-card-header>
            <!-- <b-collapse id="accordion-1" accordion="my-accordion" role="tabpanel"> -->
              <b-card-body>
                <h6>{{op_name}}</h6>
                <div class="mt-3 scrollable" id="list-operator">
                  <b-table
                    striped
                    hover
                    :filter="to_regex(op_material)"
                    :filter-included-fields="op_filt_on"
                    :items="operator_items"
                    head-variant="dark"
                  ></b-table>
                </div>
              </b-card-body>
            <!-- </b-collapse> "to_regex(op_material)"-->
          </b-card>
        </div>
        <b-button class="mt-3" variant="secondary" @click="hide_modal('operator-modal-ref')">Tutup</b-button>
      </b-modal>
    </b-row>  
   
    <!-- <hr/> -->
    <b-row class="my-2">
      <b-col>
        <h6>Nomor PO : {{no_po}}</h6>
      </b-col>
      <!-- <b-col>
        <b-pagination
          v-model="material_filter"
          :total-rows="material_total_rows"
          :per-page="material_per_page"
          align="fill"
          size="sm"
          class="my-0"
        ></b-pagination>
      </b-col> -->
    </b-row>
    <!-- :filter-included-fields="material_filt_on"
        :filter="`${material_filter}`" -->
    <div class="mt-3 scrollable" id="list-material" v-if="istableloaded">
      <b-table
        striped
        hover
        fixed
        :fields="material_fields"
        :items="material_items"
        head-variant="dark"
      >
      <!--   -->
        <template #cell(action)="row">
          <b-button 
            size="sm" 
            @click="row.item.checklist = action_click(row.item.checklist)"
            :variant="row.item.checklist==1 ? 'success' : 'danger'"
          >
            Ada 
          </b-button>
        </template>
      </b-table>
    </div>
    <b-alert class="mt-2 container" v-model="save_alert" dismissible variant="danger">
      Material Belum Lengkap!
    </b-alert>
    <b-row class="my-2 mx-0">
      <b-button v-if="istableloaded" block @click="save_click(no_po)" variant="dark" > Simpan </b-button>
    </b-row>
  </b-container>
  
</template> 

<script> 
import axios from "axios";

export default {
  name: "Ongoing",
  components: {
  },
  data(){
    return{
      save_alert:false,
      qr_alert: false,
      istableloaded: false,
      form_qr:'',
      no_po:'',
      po_items : [],
      op_name:'',
      op_material:[],
      op_filt_on:["Material"],
      material_total_rows:1,
      material_per_page:1,
      material_filter :1,
      material_filt_on:["batch"],
      material_fields: [
        { key: 'material', label: 'Material' },
        { key: 'target_weight', label: 'Target' },
        { key: 'actual_weight', label: 'Aktual' },
        { key: 'action', label: 'Aksi' },
      ],
      material_items: [],
      operator_assign: [
          {name: 'Timbangan 1', material:['AAA', 'BBB']},
          {name: 'Timbangan 2', material:['AAA', 'BBB']},
          {name: 'Timbangan 3', material:['AAA', 'BBB']},
          {name: 'Timbangan 4', material:['AAA', 'BBB']},
          {name: 'Timbangan 5', material:['CCC', 'DDD']},
          {name: 'Timbangan 6', material:['CCC', 'DDD']},
          {name: 'Timbangan 7', material:['CCC', 'DDD']},
          {name: 'Timbangan 8', material:['CCC', 'DDD']},
      ],
      operator_items: [
        {
          Material: "AAA",
          Berat: "25",
        },
        {
          Material: "BBB",
          Berat: "225",
        },
      ],
    }
  },
  mounted() {
  },
  methods:{
    onsubmitdecode(event){
      event.preventDefault();
    },
    dd_event(){
      axios
      .get(`/api/checklist/code`, {
        timeout: 5000,
      }) 
      .then((res) => {
        if (res.data.length > 0) {
          this.po_items = res.data;
          console.log(res.data)
        } 
      })
      .catch((err) => {
        console.error(err);
      });
    },
    dd_item_click(code){
      axios
        .get(`/api/checklist/materials?code='${code}'`, {
          timeout: 5000,
        })
        .then((res) => {
          if (res.data.length > 0) {
            this.no_po = code;
            this.material_items = res.data;
            this.material_total_rows = res.data.length;
            this.material_per_page = Math.floor(res.data.length/Math.max.apply(Math, res.data.map(function(o) { return o.batch; })));
            // console.log(this.material_items)
            this.istableloaded = true;
          } 
          else {
            this.istableloaded = false;
          }
        })
        .catch((err) => {
          this.weigh_inactive = true;
          console.error(err);
        });
    },
    onDecode(qrcode) {
      let po = qrcode.split(";")[0];
      let batch= qrcode.split(";")[1];
      this.qr_alert=false;
      axios
      .get(`/api/checklist/materials?code='${po}'&batch=${batch}`, {
        timeout: 5000,
      })
      .then((res) => {
        
        if (res.data.length > 0) {
          this.no_po = po;
          this.material_items = res.data;
          this.material_total_rows = res.data.length;
          this.material_per_page = Math.floor(res.data.length/Math.max.apply(Math, res.data.map(function(o) { return o.batch; })));
          // console.log(this.material_items)
          this.istableloaded = true;
          this.qr_alert=false;
          this.hide_modal('qrscan-modal-ref');
        } 
        else {
          this.istableloaded = false;
          this.qr_alert=true;
          this.hide_modal('qrscan-modal-ref');
        }
      })
      .catch((err) => {
        this.weigh_inactive = true;
        this.qr_alert=true;
        this.hide_modal('qrscan-modal-ref');
        console.error(err);
      });

      this.form_qr = '';
    },

    async onInit(promise) {
      try {
        await promise;
      } catch (error) {
        if (error.name === "NotAllowedError") {
          this.error = "ERROR: you need to grant camera access permisson";
        } else if (error.name === "NotFoundError") {
          this.error = "ERROR: no camera on this device";
        } else if (error.name === "NotSupportedError") {
          this.error = "ERROR: secure context required (HTTPS, localhost)";
        } else if (error.name === "NotReadableError") {
          this.error = "ERROR: is the camera already in use?";
        } else if (error.name === "OverconstrainedError") {
          this.error = "ERROR: installed cameras are not suitable";
        } else if (error.name === "StreamApiNotSupportedError") {
          this.error = "ERROR: Stream API is not supported in this browser";
        }
      }
    },
    op_click(name, material){
      this.op_name=name;
      this.op_material=material;
    },
    hide_modal(ref) {
      this.$refs[ref].hide();
    },
    action_click(val){
      return !val
    },
    save_click(code){
     
      if (!this.material_items.map((o) => o.checklist).includes(false)){
        axios
        .put(`/api/checklist/materials?code='${code}'`,  
        {
          timeout: 5000,
          data : this.material_items
        })
        .then((res) => {
          console.log(res)
          this.no_po = '';
          this.material_items = [];
          this.material_total_rows = 0;
          this.material_per_page = 0;
          this.istableloaded = false;
          this.save_alert=false;
        })
        .catch((err) => {
          this.save_alert=true;
          console.error(err);
        });
      }
      else {
        this.save_alert=true;
      }
    },
    to_regex(arr){
      let regex= arr[0];
      for (const str of arr) {
        regex = regex + "|" + str;
      }

      return new RegExp(regex);
    }
  }
};
</script>
